	module.exports = {"data":{"markdownRemark":{"html":"<p>A resident I met that day described it as hell’s armpit, though the statement ended in a chuckle. I was in one of the last working class neighborhoods left in San Francisco, at an event called Sunday Streets, which monthly hops from neighborhood to neighborhood transforming one of its main streets in to a street fair, inviting local organizations to create a physical presence to engage with the community. I was volunteering with an organization called ArtSpan. We wanted to get the message out that we’re renovating the neighborhood’s old defunct hospital building to be <a href=\"https://www.artspan.org/artspan-onondaga-art-center\">ArtSpan’s new home</a> which will offer community programs and studio space for artists and other efforts to keep art alive in San Francisco, thanks to a low interest lease from the city.</p>\n<p>To watercolor paint in spray bottles, various wooden shapes for doing <a href=\"https://en.wikipedia.org/wiki/Resist\">resist</a>, pens, brushes, little blank white canvases with convenient built-in keyhole fittings for hanging up we dedicated one of our tables so we could invite passers by, especially children, to create a masterpiece they can take home. And create they did. I was blown away by the worthy art they created. Young and old, black, white, Latino, Asian, etc they came, they saw, they painted.</p>\n<p><img src=\"./artspan-table.jpeg\" alt=\"ArtSpan table\">\n<img src=\"/art-pieces-on-ground-ab3434b20c99361d26a33d68316f61f9.jpeg\" alt=\"Finished peices\">\n<img src=\"/art-pieces-on-table-35f5954fdb02b46d623cde739d92b312.jpeg\" alt=\"More finished peices\"></p>\n<p>I stood in front of the tent, trying various tactics to get people’s attention and convince them to give it a try. I drew an advertisement on the asphalt with some of the colored chalk floating around. I held up finished pieces and waved them at the crowds. I thought I could sense something, fear or uncertainty, with some people. They seemed to want to participate but something held them back. It was easier to melt this inhibition with the kids, showing them how much fun people were having. With the adults, the defenses seemed much thicker.</p>\n<p><img src=\"/chalk-drawing-9d2edaa45c0dcc8c9015d9370a98449e.jpeg\" alt=\"“Art this way” chalk drawing\"></p>\n<p>The team consisted entirely of volunteers like myself. <a href=\"http://www.amyahlstrom.com/\">Amy, a real professional artist</a> was the mastermind and ringleader of our operation. Her good vibes and enthusiasm, she was a teaching artist for many years, really helped keep people engaged and having fun once I snagged their attention. There was also live painting by <a href=\"https://www.nicolevismara.com/\">Nicole Vismara</a> to help get people’s attention.</p>\n<p><img src=\"/artspan-table-sideview-62c2764d83ad67e574a318fc2eb841a2.png\" alt=\"ArtSpan table, again\">\n<img src=\"/live-painting-bb4e838b1558260508092078bd6cdb5e.png\" alt=\"Live painting\"></p>\n<p>As the show was drawing to a close, the MC on the live music stage thanked the audience for keeping the Excelsior real. This is what “real” means, I thought as I walked back to where I parked my bike. In the slanted golden afternoon sunbeams I saw life being lived on the sidewalks. Folks just chatting, getting their hair cut, kids playing, a few broken windows, and a conspicuous absence of new housing developments, shiny new coffee shops, Disneyland for well-healed millennials, etcetera, etcetera. I felt a raw striving energy, a bittersweet happiness. Something about it stole a piece of my heart. The name Excelsior can mean “excellent”, as well as “ever upward”. It’s already lived up to the first sense, in my opinion. I hope the name is prophetic in the second sense only if it doesn’t entail the creative destruction that has effected so many other parts of this city. What is too often destroyed is what gives the city that ineffable quality referred to as “soul”. I know this is an old story, that San Francisco isn’t what it used to be. I’ve heard it over and over again since moving here and often worried that I was part of the problem. The experience of volunteering and the prospective creation of the Onondaga Art Center in the old hospital building has given me more hope than anything else that the best parts of the old San Francisco are still alive, like hot coals hiding under the ashes of a mostly extinguished fire. A fire which can be rekindled.</p>\n<p>These past few weeks I’ve watched my monetary wealth steadily decline due to my determination to do only what I really care about, regardless of the compensation, for as long as I can manage to do so. As I rode home in the late afternoon sunlight through the hills of the outskirts of town I felt wealthy with renewed faith, passion and purpose.</p>","excerpt":"A resident I met that day described it as hell’s armpit, though the statement ended in a chuckle. I was in one of the last working class neighborhoods left in San Francisco, at an event called Sunday Streets, which monthly hops from neighborhood to…","frontmatter":{"date":"March 29, 2018","path":"/blog/2018/3/29/fostering-creativity-excelsior","title":"Fostering creativity in the Excelsior district of San Francisco","category":"walking the talk"}}},"pathContext":{}}

